<template>
    <div 
    v-if="isOpen"
    @click.stop
    :style="{top: position.top + 'px', left: position.left + 'px'}"
    class="context-menu">
        <slot></slot>
    </div>
</template>

<script>

export default {  
    name: 'ContextMenu',
    props: {
        position: {
            type: Object,
            required: true
        },
        isOpen: {
            type: Boolean,
            required: true
        }
    },
    emits: {
        close: null,
        open: null
    },
    beforeUpdate(e) {
        if (this.isOpen){
            setTimeout(() => {
                document.addEventListener('click', this.hideContextMenu);
                document.addEventListener('contextmenu', this.hideContextMenu);
                document.addEventListener('keydown', this.hideByKeyboard);
                if (!this.isOpen){
                    this.$emit('open');
                }
            }, 0);
        } else {
            document.removeEventListener('click', this.hideContextMenu);
            document.removeEventListener('contextmenu', this.hideContextMenu);
            document.removeEventListener('keydown', this.hideByKeyboard);
        }
        if (document.documentElement.clientWidth - this.position.left <= 250){
            this.position.left -= 250;
        }
    },
    methods: {
        hideContextMenu(){
            this.$emit('close');
        },
        hideByKeyboard(e){
            if (e.key === 'Escape'){
                this.hideContextMenu();
            };
        }
    }
}
</script>

<style lang="scss">
    .context-menu{
        @include container(false);
        background: $bg-color-white-light;
        position: fixed;
        flex-direction: column;
        width: 250px;
        z-index: 10;
        backdrop-filter: blur(10px);
        nav{
            li{
                display: flex;
                box-sizing: border-box;
                overflow: hidden;
                width: 100%;
                padding: 20px;
                justify-content: center;
                cursor: pointer;
                @include transition;
            }
            li:not(:last-child){
                border-bottom: 1px solid $bor-color-grey;
            }
            li:hover{
                background: $bor-color-grey;
            }
        }
    }
</style>