<template>
    <transition name="fade">
        <context-menu
            :isOpen="contextMenu"
            :position="contextMenuPosition"
            @close="contextMenu  = false"
            @open="contextMenu = true">
            <nav>
                <li>Переименовать {{meta.id}}</li>
                <li>Удалить</li>
                <li>Пункт 1</li>
                <li>Пункт 1</li>
                <li>Пункт 1</li>
                <li>Пункт 1</li>
            </nav>
        </context-menu>
    </transition>
    <div class="project-layout" 
        :style="backgroundImage"
        :class="{'background-image': bgImage}"
        @contextmenu.prevent="showContextMenu($event)">
        <router-link 
            :to="'/projects/' + meta.id"></router-link>
        <div class="project-layout-title">
            <h4>{{meta.title}}</h4>
        </div>
        <div 
            class="project-layout-settings"
            @click="showContextMenu($event)">
            <font-awesome-icon icon="ellipsis-v"/>
        </div>
    </div>
</template>

<script>
import contextMenu from "@/components/popups/ContextMenu.vue";

export default {
    name: 'ProjectLayout',
    components: {
        contextMenu
    },
    data(){
        return {
            bgImage: false,
            contextMenu: false,
            contextMenuPosition: {
                top: null,
                left: null
            }
        }
    },
    computed: {
        backgroundImage(){
            if(this.meta.background !== undefined && this.meta.background !== null && this.meta.background !== ''){
                this.bgImage = true;
                return `--background: url(${this.meta.background})`;
            }
            else{
                return '';
            }
        }
    },
    methods:{
        showContextMenu(e) {
            this.contextMenu = true;
            this.contextMenuPosition.top = e.clientY;
            this.contextMenuPosition.left = e.clientX;
        },
    },
    props: {
        meta: {
            type: Object,
            required: true
        }
    }
}
</script>

